const openModalBtn = document.querySelector('#registration-modal-open');
const modal = document.querySelector('#registration-modal');
const closeModalBtn = document.querySelector('.registration-modal-close');
const modalContainer = document.querySelector('.registration-modal-background');
const singinBTN = document.querySelector('.sing-in--btn');
const singModal = document.querySelector('#SignIn-modal');

singinBTN.addEventListener('click', () => {
  modal.classList.add('is-hidden');
  singModal.classList.remove('is-hidden');

})

openModalBtn.addEventListener('click', () => {
  modal.classList.remove('is-hidden');
});

closeModalBtn.addEventListener('click', () => {
  modal.classList.add('is-hidden');
  singModal.classList.add('is-hidden');

});

modalContainer.addEventListener('click', (event) => {
  if (event.target === modalContainer) {
    modal.classList.add('is-hidden');
    singModal.classList.add('is-hidden');
  }
});

window.addEventListener('keydown', (event) => {
  if (event.key === 'Escape') {
    modal.classList.add('is-hidden');
    singModal.classList.add('is-hidden');
  }
});